import { Program, Output, Tape, Register } from './Display';
import { CustomGrid } from '../helpers';
import { Grid, Typography, TextField } from '../components/mui';
import { Toolbar } from './Toolbar';
import { COLORS, SPACING, TYPOGRAPHY } from '../config/theme';

import React, { createContext, useContext } from 'react';

export const EditorContext = createContext();

export default function Editor({ container, sideProps, hide, children }) {
    const { name, tapeFlag, outFlag, regFlag, code } =
        useContext(EditorContext);

    const rightProps = { xs: 6, md: 4 };
    let display, leftProps;

    if (hide) {
        display = 'none';
        leftProps = 12;
    } else {
        leftProps = { xs: 6, md: 8 };
        display = 'flex';
    }

    const titleProps = {
        size: 'grow',
        sx: {
            display: {
                xs: 'none',
                md: 'block',
            },
        },
    };

    const contentProps = {
        flex: 1,
        spacing: 2,
        ref: container,
        container: true,
        display: 'flex',
        alignItems: 'center',
        sx: { overflowY: 'auto' },
    };

    return (
        <Grid
            container
            spacing={2}
            height="100vh"
            display="flex"
            flexDirection="column"
            padding="5vh 5vw 5vh 5vw"
            sx={{
                background: COLORS.surface.background,
                position: 'relative',
            }}
        >
            <Grid container alignItems="center" justifyContent="space-between">
                <Grid {...titleProps}>
                    <Typography
                        variant="h4"
                        sx={{
                            fontSize: TYPOGRAPHY.fontSize.h2,
                            fontWeight: TYPOGRAPHY.fontWeight.semibold,
                        }}
                    >
                        {name}
                    </Typography>
                </Grid>
                <Toolbar />
            </Grid>
            <Grid {...contentProps}>
                <Grid size={leftProps}>{children}</Grid>
                <Grid display={display} size={rightProps}>
                    <TextArea {...sideProps} />
                </Grid>
            </Grid>
            <Grid container spacing={2}>
                {(() => {
                    const fields = [];
                    const fieldCount = [
                        code !== undefined,
                        tapeFlag,
                        outFlag,
                        regFlag,
                    ].filter(Boolean).length;
                    const gridSize =
                        fieldCount === 1
                            ? { xs: 12, md: 12 }
                            : { xs: 12, md: 6 };

                    if (code !== undefined) {
                        fields.push(
                            <Grid key="program" size={gridSize}>
                                <Program />
                            </Grid>
                        );
                    }
                    if (tapeFlag) {
                        fields.push(
                            <Grid key="tape" size={gridSize}>
                                <Tape />
                            </Grid>
                        );
                    }
                    if (outFlag) {
                        fields.push(
                            <Grid key="output" size={gridSize}>
                                <Output />
                            </Grid>
                        );
                    }
                    if (regFlag) {
                        fields.push(
                            <Grid key="register" size={gridSize}>
                                <Register />
                            </Grid>
                        );
                    }

                    return fields;
                })()}
            </Grid>
        </Grid>
    );
}

export function GridArea({ handleClick, chooseColor, options, rows, cols }) {
    const { size } = useContext(EditorContext);

    const cellProps = (row, col) => {
        const pos = cols * row + col;
        const color = chooseColor(pos);
        const value = options[pos] || ' ';

        // Consistent glassmorphism color scheme
        const getCellStyles = color => {
            const styles = {
                primary: {
                    bg: COLORS.surface.glass,
                    text: COLORS.primary.main,
                    border: `1px solid ${COLORS.primary.main}`,
                    hover: COLORS.interactive.selected,
                },
                info: {
                    bg: COLORS.surface.glass,
                    text: COLORS.text.primary,
                    border: `1px solid ${COLORS.border.subtle}`,
                    hover: COLORS.interactive.hover,
                },
                secondary: {
                    bg: COLORS.surface.glass,
                    text: COLORS.text.secondary,
                    border: `1px solid ${COLORS.border.subtle}`,
                    hover: COLORS.interactive.selected,
                },
            };
            return styles[color] || styles.secondary;
        };

        const cellStyle = getCellStyles(color);

        return {
            color: cellStyle.text,
            backgroundColor: cellStyle.bg,
            onClick: handleClick(pos),
            children: <Text text={value} />,
            sx: {
                borderRadius: SPACING.borderRadius.md,
                border: cellStyle.border,
                cursor: 'pointer',
                transition: 'all 200ms cubic-bezier(0.4, 0, 0.2, 1)',
                backdropFilter: 'blur(24px) saturate(180%)',
                '&:hover': {
                    backgroundColor: cellStyle.hover,
                },
            },
        };
    };

    return (
        <CustomGrid cellProps={cellProps} size={size} rows={rows} cols={cols} />
    );
}

export function TextArea({
    value,
    readOnly,
    infoLabel,
    fillValue,
    handleChange,
}) {
    const { height } = useContext(EditorContext);
    const rows = Math.floor(height / 32);

    fillValue = fillValue || 'Hello, World!';
    infoLabel = infoLabel || 'Program code';
    readOnly = readOnly || false;

    // Use controlled component if value is provided, otherwise use uncontrolled
    const isControlled = value !== undefined && value !== null;
    const textFieldProps = isControlled
        ? {
              value: value || '',
              onChange: handleChange,
          }
        : {
              defaultValue: fillValue,
          };

    return (
        <TextField
            variant="outlined"
            label={infoLabel}
            slotProps={{
                inputLabel: { shrink: true },
                htmlInput: { readOnly },
            }}
            fullWidth
            multiline
            rows={rows}
            {...textFieldProps}
            sx={{
                '& .MuiInputBase-root': {
                    alignItems: 'flex-start',
                    backgroundColor: COLORS.surface.glass,
                    backdropFilter: 'blur(24px) saturate(180%)',
                    borderRadius: SPACING.borderRadius.md,
                    border: `1px solid ${COLORS.border.subtle}`,
                },
                '& .MuiInputBase-input': {
                    fontFamily: 'monospace',
                    color: 'text.primary',
                },
                '& .MuiOutlinedInput-notchedOutline': {
                    borderColor: COLORS.border.subtle,
                },
                '&:hover .MuiOutlinedInput-notchedOutline': {
                    borderColor: COLORS.border.subtle,
                },
                '&.Mui-focused .MuiOutlinedInput-notchedOutline': {
                    borderColor: 'primary.light',
                },
                '& .MuiInputLabel-root': {
                    color: 'text.secondary',
                },
                '& .MuiInputLabel-root.Mui-focused': {
                    color: 'primary.light',
                },
            }}
        />
    );
}

export function Text({ text, ...props }) {
    return (
        <Typography
            {...props}
            sx={{
                fontSize: 'inherit',
                fontWeight: 'inherit',
                fontFamily: 'inherit',
                userSelect: 'none',
                lineHeight: 1,
                ...props.sx,
            }}
        >
            {text}
        </Typography>
    );
}
